const Chart = styled.svg`
  height: 45vh;
  width: auto;
  justify-content: center;

  &.techHolder__chart {
    fill: none;
    stroke: #4cc790;
    stroke-linecap: round;
    stroke-width: 1.8;
    stroke-dasharray: ${(props) => props.percent || 0} 100;
    stroke-dashoffset: -200;
  }

  & > .techHolder__chartbase {
    fill: none;
    stroke: #ddd;
    stroke-width: 1.8;
    stroke-dasharray: 0;
  }
  @media screen and (max-width: 700px) {
    height: 30vh;
  }
`;

const ChartDesc = styled.div`
  min-width: 80%;
  position: absolute;
  text-align: center;
  left: 50%;
  top: 45%;
  transform: translate(-50%, -50%);

  & > .job_name {
    font-size: 1.4rem;
    line-height: 300%;
  }
`;

export default function ContributionChart(props) {
  const [isSolo, setSolo] = useState(true);
  useEffect(() => {
    if (props.withTeam.length === 0) {
      setSolo(true);
    } else {
      setSolo(false);
    }
  });

  return (
    <div
      className="ChartContainer"
      style={{ margin: "auto", position: "relative" }}
    >
      <Chart
        className="techHolder__chart"
        percent={props.percent}
        viewBox="0 0 36 36"
      >
        <path
          className="techHolder__chartbase"
          d="M18 2.0845
            a 15.9155 15.9155 0 0 1 0 31.831
            a 15.9155 15.9155 0 0 1 0 -31.831"
        />
        <path
          d="M18 2.0845
            a 15.9155 15.9155 0 0 1 0 31.831
            a 15.9155 15.9155 0 0 1 0 -31.831"
        />
      </Chart>
      <ChartDesc className="techHolder__desc">
        <strong className="job_name">{props.jobName}</strong>
        <br />

        <strong>MY JOB</strong>
        <br />
        {props.myJob.map((item, idx) => {
          return (
            <span key={"myjob" + idx}>
              {item}
              <br />
            </span>
          );
        })}
        <br />
        {isSolo || (
          <div>
            <strong>WITH TEAM</strong>
            <br />
            {props.withTeam.map((item, idx) => {
              return (
                <span key={"w/team" + idx}>
                  {item}
                  <br />
                </span>
              );
            })}
          </div>
        )}
      </ChartDesc>
    </div>
  );
}
