import React from "react";
import styled from "styled-components";

const Button = styled.button`
  display: inline-block;
  font-size: 1.2rem;
  font-weight: 600;
  width: auto;
  margin: auto;
  grid-column: span 3;
  padding: 1rem 2rem;
  color: white;
  background-color: #4cc790;
  border: 2px black solid;
  cursor: pointer;
  transition: 0.2s;
  &:hover {
    background-color: #79dfb1;
  }
  &:active {
    background-color: #3f9b72;
  }
`;

export default function LinkButton(props) {
  return (
    <a href={props.link} target="_blank">
      <Button>{props.btnText || "사이트로 가기"}</Button>
    </a>
  );
}
